@mixin positionTopLeft($position, $top, $left) {
    position: $position;
    top: $top;
    left: $left;
  }

@mixin dimensions($width, $height) {
    width: $width;
    height: $height;
}

.education {
    font-size: 25px;
    color: white;
    user-select: none;
    text-align:justify;

    p { text-indent: 30px; }

    @media (max-width: 600px) {
        // text-align: left;
    }
}

.introduction {
    @include positionTopLeft(absolute, 50%, 50%);
    transform: translate(-50%, -50%);
    @include dimensions(590px, 350px);
    margin: 5px;

    button {
        @include positionTopLeft(relative, -55px, 350px);
    }

    @media (max-width: 500px) {
        @include dimensions(90%, auto);

        button {
            @include positionTopLeft(relative, 0px, 40%);
        }
    }

    @media (min-width: 500px) and (max-width: 1100px) {
        @include dimensions(450px, 490px);

        button {
            @include positionTopLeft(relative, 0px, 200px);
        }
    }
}

.navbarChapter {
    @include positionTopLeft(absolute, 405px, 430px);

    @media (max-width: 800px) {
        @include positionTopLeft(absolute, 440px, 50px);
        width: 80%;
    }
}

.navbarIconsChapter {
    @include positionTopLeft(absolute, 105px, 420px);
    @include dimensions(470px, 80px);

    @media (max-width: 600px) {
        @include positionTopLeft(absolute, 45px, 90px);
        @include dimensions(70%, auto);
    }

    @media (min-width: 600px) and (max-width: 900px) {
        @include positionTopLeft(absolute, 105px, 270px);
        @include dimensions(50%, auto);
    }
}

.menuChapter {
    @include positionTopLeft(absolute, 100px, 50%);
    transform: translate(-50%);
    @include dimensions(470px, auto);

    @media (max-width: 600px) {
        @include positionTopLeft(absolute, 10px, 170px);
        transform: translate(0);
        @include dimensions(53%, auto);
    }
}

.createMapChapter {
    @include positionTopLeft(absolute, 170px, 360px);
    @include dimensions(470px, auto);

    button {
    @include positionTopLeft(relative, -50px, 330px);
    }
    
    @media (max-width: 600px) {
        @include positionTopLeft(absolute, 35px, 170px);
        @include dimensions(53%, auto);

        button {
            @include positionTopLeft(relative, -20px, 60%);
            }
    }

    @media (min-width: 600px) and (max-width: 900px)  {
        @include positionTopLeft(absolute, 170px, 280px);
        @include dimensions(53%, auto);
        p { text-indent: 80px; }
    }
}

.createMapMoveItemsChapter {
    @include positionTopLeft(absolute, 125px, 50%);
    transform: translate(-50%);
    @include dimensions(530px, 200px);

    @media (max-width: 1100px) {
        @include positionTopLeft(absolute, 15px, 230px);
        transform: translate(0);
        @include dimensions(330px, 290px);
    }
}

.diceChapter {
    @include positionTopLeft(absolute, 200px, 50%);
    transform: translate(-50%, 0);
    @include dimensions(470px, 150px);

    @media (max-width: 1100px) {
        @include positionTopLeft(absolute, 200px, 270px);
        transform: translate(0, 0);
    }
}

.clickDiceMenuChapter {
    @include positionTopLeft(absolute, 205px, 400px);

    @media (max-width: 1100px) {
        @include positionTopLeft(absolute, 205px, 200px);
    }
}
.changeDiceChapter {
    @include positionTopLeft(absolute, 80px, 460px);
    @include dimensions(450px, 120px);
}

.icons {
    transform: translate(0, -50%);
    padding: 10px;
    background-color: #1c1824;
    border-radius: 10px;
    font-size: 15px;
    z-index: 5;
}

.icons::after {
    content: ''; 
    @include positionTopLeft(absolute, 50%, -25px);
    transform: translate(0, -50%);
    border: 15px solid transparent;
    border-right: 20px solid #1c1824;
}

