.EndChapter {
    @include positionTopLeft(absolute, 50%, 50%);
    transform: translate(-50%, -50%);
    padding: 50px;
    border-radius: 25px;
    @include dimensions(470px, auto);
    background-color: white;
    color: black;
    z-index: 3;

    button {
        @include positionTopLeft(relative, 0px, 250px);
        margin: 5px;
    }

    img {
        @include dimensions(50px, 50px);
    }
    .firework_1 {
        @include positionTopLeft(absolute, 40px, 380px);
    }
    .firework_2 {
        @include positionTopLeft(absolute, 200px, 100px);
    }

    @media (max-width: 600px) {
        @include positionTopLeft(absolute, 50%, 100px);
        transform: translate(0, -50%);
        @include dimensions(55%, auto);
        padding: 30px;

        button {
            @include positionTopLeft(relative, 0px, 50%);
            transform: translate(-50%);

            margin: 0;
        }
        .firework_1 {
            @include positionTopLeft(absolute, auto, 75%);
        }
        .firework_2 {
            @include positionTopLeft(absolute, auto, 10%);
        }
    }
}