.menu {
    position: fixed;
    background-color: white;
    border: 1px solid black;
    z-index: 100;
    cursor: pointer;
  }
  
  ul,
  li {
    padding: 0;
    margin: 0;
  }
  
  li {
    padding: 10px;
    list-style-type: none;
  }
  
  li:hover {
    background-color: #dbdbdb;
  }