@mixin position($position, $top, $right, $bottom, $left) {
  position: $position;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
}

@mixin possitionLeft($position, $left) {
  position: $position;
  left: $left;
}

@mixin dimensions($width, $height) {
  width: $width;
  height: $height;
}

@mixin border($color, $radius) {
  border: $color solid 1px;
  border-radius: $radius;
}

.App {
  display: grid;

  position: fixed;

  @include dimensions(100%, 100%);

  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 20px;
  word-wrap:break-word;

  background-color: #85728a; 
  // #1c1824

  input {
    @include dimensions(97px, 20px);
  }
  .color {
    @include dimensions(105px, 27px);
  }

  button {
    @include dimensions(105px, 25px);
    border: none;
    background: #5adc50;
  }
  button:hover {
    background: #38cb2d; 
  }
  .button_on { 
    background: #F7941E; 
  }
  .button_on:hover { 
    background: #f48706; 
  }
  .button_off { 
    background: #8a8a8a;
  }
  .button_off:hover { 
    background: #797979; 
  }

  @media (max-width: 600px) {
    font-size: 15px;
    input {
      @include dimensions(67px, 20px);
    }
    .color {
      @include dimensions(75px, 27px);
    }
  
    button {
      @include dimensions(75px, 35px);
    }
  }
}

::-webkit-scrollbar {
  width: 5px;
}  

.header {
  @include dimensions(90%, null);
  @include possitionLeft(relative, 10px);
  padding-top: 15px;
  border-bottom: rgb(194, 198, 197) solid 1px;
}

.navbar {
  position: fixed;
  padding: 5px;
  z-index: 1;
  background-color: #f4f4f4;
  @include dimensions(75px, 100%);

  img {
    @include dimensions(60px, 60px);
    @include possitionLeft(relative, 50%);
    top: 10px;
    transform: translate(-50%)
  }

  @media (max-width: 600px) {
    @include dimensions(45px, 100%);

    img {
      @include dimensions(50px, 50px);
      @include possitionLeft(relative, 50%);
      top: 10px;
      transform: translate(-50%)
    }
  }
}

.menu {
  @include dimensions(125px, 100%);
  @include possitionLeft(fixed, 85px);

  z-index: 2;
  background-color: #d5d5d5;
  overflow-y: auto;

  @media (max-width: 600px) {
    @include dimensions(95px, 100%);
    @include possitionLeft(fixed, 55px);
  }
}

.map {
  @include possitionLeft(fixed, 55%);
  top: 50%;
  transform: translate(-55%, -50%);
}

